<template>
  <div class="diary">
    <h3>Дневник</h3>
    <Loader v-show="loading" />
    <div v-show="!loading" class="input-field width col s7">
      <select ref="weekSelect" @change="selectWeek" v-model="selectedWeek">
        <option v-for="(week, index) in weeks" :key="index" :value="index">
          {{ week.start | date }} - {{ week.end | date }}</option
        >
      </select>
      <label>Выбери неделю</label>
    </div>
    <div v-show="!loading" class="row width">
      <button
        @click="selectWeek(-1)"
        class="btn waves-effect waves-light light-blue darken-1 col s4"
      >
        <i class="material-icons">navigate_before</i>
      </button>
      <button
        @click="selectWeek(1)"
        class="btn waves-effect waves-light light-blue darken-1 col push-s4 s4"
      >
        <i class="material-icons">navigate_next</i>
      </button>
    </div>
    <div
      v-if="!loading && countdown.work"
      style="margin-top: 15px;"
      @click="cancelCountdown"
      class="progress width"
    >
      <div
        class="determinate"
        :style="{
          width:
            countdown.left_time / ((this.settings.diaryCountdown || 5) * 10) +
            '%'
        }"
      ></div>
    </div>
    <div id="result" v-if="!loading">
      <Day v-for="day in diary" :key="day.date" :day="day" />
    </div>
    <transition name="appearance">
      <ViewMark
        v-if="viewMark.show"
        :id="viewMark.id"
        :mark="viewMark.mark"
        :type="viewMark.type"
        @close-mark="viewMark.show = false"
      />
    </transition>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Day from "@/components/DiaryDay";
import Loader from "@/components/Loader";
import ViewMark from "@/components/ViewMark";

export default {
  data: () => ({
    weeks: [],
    diary: [],
    allTypes: [],
    viewMark: {
      show: false,
      id: null,
      mark: null,
      type: null
    },
    loading: true,
    instance: null,
    countdown: {
      work: false,
      left_time: 0,
      old_index: 0,
      interval: null
    },
    selectedWeek: null
  }),
  computed: mapState({
    settings: state => state.settings.storage
  }),
  methods: {
    ...mapActions(["GetTypes", "GetRange", "GetDiary"]),
    init(range) {
      const now = Date.now();
      const end = new Date(range.end);
      const start = new Date(range.start);
      while (start < end) {
        const week = {
          start: new Date(start),
          end: null
        };
        do {
          start.setDate(start.getDate() + 1);
        } while (start.getDay() != 0);
        week.end = new Date(start);
        if (
          now >= week.start &&
          now <= week.end - 0 + (this.settings.noIncludeSunday ? 0 : 8.64e7)
        ) {
          this.selectedWeek = this.weeks.length;
        }

        this.weeks.push(week);
        start.setDate(start.getDate() + 1);
      }
      this.loading = false;
      this.loadDiary(this.weeks[this.selectedWeek]);
      if (this.instance && this.instance.destroy) this.instance.destroy();
      setTimeout(() => {
        // eslint-disable-next-line no-undef
        this.instance = M.FormSelect.init(this.$refs.weekSelect);
      }, 0);
    },
    selectWeek(event) {
      if (typeof event != "number") {
        this.loadDiary(this.weeks[this.selectedWeek]);
        return;
      }

      this.countdown.work = true;
      clearInterval(this.countdown.interval);
      this.selectedWeek = (event > 0 ? 1 : -1) + this.selectedWeek;
      this.countdown.left_time = (this.settings.diaryCountdown || 5) * 1000;
      this.countdown.interval = setInterval(() => {
        this.countdown.left_time -= 300;

        if (this.countdown.left_time <= 0) {
          this.countdown.work = false;
          clearInterval(this.countdown.interval);
          this.loadDiary(this.weeks[this.selectedWeek]);
        }
      }, 300);
      setTimeout(() => this.instance._setValueToInput(), 0);
    },
    loadDiary(range) {
      this.loading = true;
      this.GetDiary(range)
        .then(
          ({ weekDays }) => ((this.loading = false), (this.diary = weekDays))
        )
        .catch(err => {
          this.loading = false;
          this.cancelCountdown();
          if (err.code == 4008) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Сайт лег, попробуй позже" });
          } else if (err.code == 4006) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
          } else {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
          }
          console.error(err);
        });
    },
    showMark({ id, mark, type }) {
      this.viewMark.id = id;
      this.viewMark.mark = mark;
      this.viewMark.type = this.allTypes.find(t => t.id == type)?.name;
      this.viewMark.show = true;
    },
    cancelCountdown() {
      this.countdown.work = false;
      clearInterval(this.countdown.interval);
      this.selectedWeek = this.countdown.old_index;
      setTimeout(() => this.instance._setValueToInput(), 0);
    }
  },
  watch: {
    selectedWeek(_val, oldVal) {
      if (oldVal) this.countdown.old_index = oldVal;
    }
  },
  filters: {
    date(val) {
      function num(val) {
        if (val < 10) val = "0" + val;
        return val;
      }
      return `${num(val.getDate())}.${num(val.getMonth() + 1)}.${num(
        val.getFullYear()
      )}`;
    }
  },
  created() {
    this.GetTypes()
      .then(types => ((this.allTypes = types), this.GetRange()))
      .then(range => this.init(range))
      .catch(err => {
        this.loading = false;
        this.cancelCountdown();
        if (err.code == 4008) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Сайт лег, попробуй позже" });
        } else if (err.code == 4006) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
        } else {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
        }
        console.error(err);
      });
  },
  destroyed() {
    if (this.countdown.work) clearInterval(this.countdown.interval);
    if (this.instance && this.instance.destroy) {
      this.instance.destroy();
    }
  },
  components: {
    Day,
    Loader,
    ViewMark
  }
};
</script>

<style scoped>
@import "../assets/diary.css";
</style>
