<template>
  <div class="card" :class="{ stripes }">
    <h5 class="center">{{ day.date | date }}</h5>
    <Lesson
      v-for="lesson in day.lessons"
      :key="lesson.classmeetingId"
      :name="lesson.subjectName"
      :room="lesson.room"
      :marks="marks(lesson.assignments)"
      :number="lesson.number"
      :endTime="lesson.endTime"
      :homework="homework(lesson.assignments)"
      :startTime="lesson.startTime"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
import Lesson from "./DiaryLesson";

export default {
  props: ["day"],
  computed: mapState({
    stripes: state => state.settings.storage.diaryStripes
  }),
  methods: {
    marks(assignments) {
      return (
        assignments
          ?.filter(v => v.mark)
          ?.map(v => ({
            id: v.id,
            mark: v.mark.mark,
            type: v.typeId
          })) || []
      );
    },
    homework(assignments) {
      return assignments?.find(m => m.typeId == 3)?.assignmentName || "";
    }
  },
  filters: {
    date(val) {
      if (typeof val == "string") val = new Date(Date.parse(val));
      const week = ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
      const month = [
        "янв",
        "фев",
        "мар",
        "апр",
        "май",
        "июн",
        "июл",
        "авг",
        "сен",
        "окт",
        "ноя",
        "дек"
      ];
      return (
        `${week[val.getDay()]}, ` +
        `${val.getDate()} ` +
        `${month[val.getMonth()]}. ` +
        `${val.getFullYear()} г.`
      );
    }
  },
  components: {
    Lesson
  }
};
</script>
