<template>
  <div class="row">
    <span class="col number s1">{{ number }}</span>
    <div class="col subject s9">
      <span class="name">{{ name }}</span>
      <span class="info">{{ startTime }} - {{ endTime }}, {{ room }} </span>
      <span class="homework" @click="showHomework">{{ homework }}</span>
    </div>
    <div class="col marks s2">
      <span
        :key="data.id"
        v-for="data in marks"
        @click="$parent.$parent.showMark(data)"
        >{{ data.mark }}</span
      >
    </div>
  </div>
</template>

<script>
export default {
  props: [
    "name",
    "room",
    "marks",
    "number",
    "endTime",
    "homework",
    "startTime"
  ],
  methods: {
    showHomework(e) {
      e.target.classList.toggle("show");
    }
  }
};
</script>
